/**
 * indexedDB
 *  IndexedDB IndexedDB 
 *  
 * IndexedDB 
 *  1.  IndexedDB object store JavaScript ""
 *  2. IndexedDB 
 *  3.  IndexedDB transaction
 *  4. IndexedDB 
 *  5. IndexedDB  LocalStorage  250MB
 *  6.  IndexedDB ArrayBuffer  Blob 
 */


 const createIndexedDB = (config) => {
    const {databaseName, version, success, fail} = config;
    const request = window.indexedDB.open(databaseName, version);
    let db = '';

    request.onerror = (e) => {
        console.log('');
        fail && fail({
            msg: '',
            e: e,
        })
    };

    request.onsuccess = (e) => {
        console.log('');
        db = request.result;
        success && success();
    };

    let objectStore = null;
    request.onupgradeneeded = function(event) {
        db = event.target.result;
        const {table_header, table_name, main_key} = config;
        if (!db.objectStoreNames.contains(table_name)) {
            objectStore = db.createObjectStore(
                table_name, 
                { 
                    keyPath: main_key, 
                    autoIncrement: true,
                }
            );

            if (table_header && Object.prototype.toString.call(table_header) === '[object Array]') {
                table_header.length > 0 && table_header.map(item => { 
                    objectStore.createIndex(item.key, item.key, { unique: item.unique ? item.unique : false });
                });
            }
        }
    };
    
    //
    const getAll = (table_name, success, fail) => {
        const objectStore = db.transaction(table_name).objectStore(table_name);
        const result = [];
        objectStore.openCursor().onsuccess = function (event) {
            const cursor = event.target.result;
            if (cursor) {
                result.push(cursor.value);
                cursor.continue();
            } else {
                console.log('');
                success && success(result);
            }
        };

        objectStore.openCursor().onerror = function (event) {
            fail && fail(event);
        }
    }

    return {
        /**
         * options
         *      table_name:  string
         *      data, array
         *      callback code1code-1 function
         */
        add: (options) => {
            const {data, table_name, callback} = options;

            if (!table_name || !data) {
                callback && callback({
                    code: -1,
                    msg: 'table_namedata'
                });
                return;
            }

            const transaction = db.transaction([table_name], 'readwrite');
            const store = transaction.objectStore(table_name);
            let _request = null;
            if (Object.prototype.toString.call(data) === '[object Array]') {
                data.length > 0 && data.map(item => {
                    _request = store.add(item);
                })
            } else if (Object.prototype.toString.call(data) === '[object Object]') {
                _request = store.add(data);
            }
            
            _request.onsuccess = function (event) {
                console.log('');
                callback && callback({
                    code: 1,
                    e: event
                })
            };

            _request.onerror = function (event) {
                console.log('');
                callback && callback({
                    code: -1,
                    e: event
                })
            }
        },
        /**
         * options
         *      table_name: 
         *      index_key: 
         *      index_value: 
         *      callback code1code-1
         */
        get: (options) => {
            const {table_name, callback, index_key, index_value} = options;
            if (!table_name) {
                callback && callback({
                    code: -1,
                    msg: 'table_name'
                });
                return;
            }

            getAll(table_name, (res) => {
                if (res && res.length > 0) {
                    let result = [];
                    if (index_value) {
                        result = res.filter(item => {
                            return item[index_key] === index_value;
                        });
                    } else {
                        result = res;
                    }
                    callback && callback({
                        code: 1,
                        data: result,
                    })
                } else {
                    callback && callback({
                        code: 1,
                        data: [],
                    })
                }
            }, (err) => {
                callback && callback({
                    code: -1,
                    e: err,
                });
            });

            // const transaction = db.transaction([table_name]);
            // const objectStore = transaction.objectStore(table_name);
            // let request = null;
            // if (index_key === objectStore.keyPath) {
            //     request = objectStore.get(index_value);
            // } else {
            //     const index = objectStore.index(index_key);//
            //     request = index.get(index_value);
            // }

            // request.onerror = function(event) {
            //     console.log('');
            //     callback && callback({
            //         code: -1,
            //         e: event,
            //     });
            // };

            // request.onsuccess = function( event) {
            //     console.log('');
            //     if (request.result) {
            //         console.log('');
            //         callback && callback({
            //             code: 1,
            //             e: event,
            //             data: request.result
            //         });
            //     } else {
            //         console.log('');
            //         callback && callback({
            //             code: 1,
            //             e: event,
            //             data: {}
            //         });
            //     }
            // };
        },
        /**
         * options
         *      table_name: 
         *      data: 
         *      callback code1code-1
         */
        update: (options) => {
            const {table_name, data, callback} = options;
            if (!table_name || !data) {
                callback && callback({
                    code: -1,
                    msg: 'table_namedata'
                });
                return;
            }

            const transaction = db.transaction([table_name], 'readwrite');
            const objectStore = transaction.objectStore(table_name);
            let request = null;
            if (Object.prototype.toString.call(data) === '[object Array]') {
                data.length > 0 && data.map(item => {
                    request = objectStore.put(item);
                })
            } else if (Object.prototype.toString.call(data) === '[object Object]') {
                request = objectStore.put(data);
            }

            request.onsuccess = function (event) {
                console.log('');
                callback && callback({
                    code: 1,
                    data: data,
                    e: event,
                })
            };

            request.onerror = function (event) {
                console.log('');
                callback && callback({
                    code: -1,
                    e: event,
                })
            }
        },
        /**
         * options
         *      table_name: 
         *      id id
         *      callback code1code-1
         */
        delete: (options) => {
            const {table_name, ids, callback} = options;
            if (!table_name || !ids) {
                callback && callback({
                    code: -1,
                    msg: 'table_nameid'
                });
                return;
            }

            const transaction = db.transaction([table_name], 'readwrite');
            const objectStore = transaction.objectStore(table_name);
            let request = null;
            if (ids && Object.prototype.toString.call(ids) === '[object Array]') {
                ids.length > 0 && ids.map(item =>{
                    request = objectStore.delete(item);
                })
            } else {
                request = objectStore.delete(ids);
            }

            request.onsuccess = function (event) {
                console.log('');
                callback && callback({
                    code: 1,
                    e: event,
                })
            };

            request.onerror = function (event) {
                console.log('');
                callback && callback({
                    code: -1,
                    e: event,
                })
            }
        },

        clear: (table_name) => {
            const transaction = db.transaction(table_name, 'readwrite');
            const store = transaction.objectStore(table_name);
            store.clear();
        },
        
        remove: (table_name) => {
            const transaction = db.transaction(table_name, 'versionchange');
            db.deleteObjectStore(table_name);
        }
    }

 }


 export default {
     createIndexedDB
};