/**
 * web()
 * 
 * 
 * 
 * DOMscrollTopclientHeightscrollHeight
 *   scrollTopY
 *   clientHeight
 *   scrollHeight
 *   scrollTop + clientHeight == scrollHeight
 *
 */

/**
 * document.body  document.documentElement
 * 
 * document.body  html dombody <body>
 * document.documentElement  html domroot  <html>
 * 
 *  scrollTop https://www.cnblogs.com/mrdooo/p/6633688.html
 *  var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
 *      
 */

// Y
function getScrollTop(dom) {
    if (dom) {
        return dom.scrollTop;
    } else {
        let scrollTop = 0;
        let bodyScrollTop = 0;
        let documentScrollTop = 0;
        if (document.body) {
            bodyScrollTop = document.body.scrollTop;
        }
        if (document.documentElement) {
            documentScrollTop = document.documentElement.scrollTop;
        }
        scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
        return scrollTop;
    }
}

// 
function getScrollHeight(dom) {
    if (dom) {
        return dom.scrollHeight;
    } else {
        let scrollHeight = 0;
        let bodyScrollHeight = 0;
        let documentScrollHeight = 0;

        if (document.body) {
            bodyScrollHeight = document.body.scrollHeight;
        }
        if (document.documentElement) {
            documentScrollHeight = document.documentElement.scrollHeight;
        }
        if (bodyScrollHeight - documentScrollHeight > 0) {
            scrollHeight = bodyScrollHeight;
        } else {
            scrollHeight = documentScrollHeight;
        }
        return scrollHeight;
    }
}

// 
function getWindowHeight() {
    let windowHeight = 0;
    if (document.compatMode === 'CSS1Compat') {
        windowHeight = document.documentElement.clientHeight;
    } else {
        windowHeight = document.body.clientHeight;
    }
    return windowHeight;
}

function onReachBottom(dom, callback, offset) {
    let node = dom ? dom : window;
    offset = offset ? offset : 0;
    node.onscroll = () => {
        if ((getScrollTop(dom) + getWindowHeight(dom) - offset) === getScrollHeight(dom)) {
            console.log('');
            if (callback && typeof callback === 'function') {
                callback();
            }
        }
    };
}


export default {
    onReachBottom,
}