/**
 * cookies
 * cookiecookie  cookie cookie
 * cookie 4KBcookie20
 * JS  APIcookiedocument.cookieHttpOnlycookie
 * 
 */

 /**
  * cookies
  *  expirescookieexpirescookieexpires GMT new Date().toGMTString() new Date().toUTCString() 
  *  domain  domaincookie
  *  path  pathcookie
  *  domainpathcookie
  *  securecookieHTTPS secure  cookie
  *  HttpOnly cookie js 
  * 
  */

/**
 * 
 * (^| ) 
 * ([^;]*)[^;]";"([^;]*)0";"
 * (;|\x24)";""\x24"
 */

function getRaw(key) {
    var reg = new RegExp("(^| )" + key + "=([^;]*)(;|\x24)"),
        result = reg.exec(document.cookie);
        
    if (result) {
        return result[2] || null;
    }

    return null;
}

function setRaw(key, value, options) {
    options = options || {};
    var expires = options.expires;
    if ('number' == typeof options.expires) {
        expires = new Date();
        expires.setTime(expires.getTime() + options.expires);
    }
    
    document.cookie =
        key + "=" + value
        + (options.path ? "; path=" + options.path : "")
        + (expires ? "; expires=" + expires.toGMTString() : "")
        + (options.domain ? "; domain=" + options.domain : "")
        + (options.secure ? "; secure" : ''); 
}



function set(key, value, options) {
    setRaw(key, encodeURIComponent(value), options);
}

function get(key) {
    var value = getRaw(key);
    if ('string' == typeof value) {
        value = decodeURIComponent(value);
        return value;
    }
    return null;
}

function remove(key, options) {
    options = options || {};
    options.expires = new Date(0);
    setRaw(key, '', options);
}


export default {
    set,
    get,
    remove,
}