/**
 * 
 * type 
 */

export default function initSeal(configs) {
    const {type='square'} = configs;
    if (type === 'square') {
        createSquareSeal(configs, true);
    } else if ('rectangle' === type) {
        createRectangleSeal(configs);
    } else if (type === 'circular') {
        createCircularSeal(configs);
    }
 }

/**
 * 
 */
 function createSquareSeal(configs, isSquare) {
    configs = configs ? configs : {};
    const div = document.createElement('div');
    div.classList.add('seal-wrap');
    const defaultStyle = {
        border: '4px solid red',
        background: 'transparent',
        borderRadius: '4px',
        lineHeight: 1,
        fontSize: '20px',
        textAlign: 'center',
        color: 'red',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        writingMode: 'tb-rl',
    };
    
    let style = Object.assign(defaultStyle, configs.style ? configs.style : {});
    for (let key in style) {
        div.style[key] = style[key];
    }

    if (isSquare) {
        div.style.width = div.style.height = configs.size ? configs.size + 'px' : '50px';
    }
    div.innerText = configs.text ? configs.text : '';
    document.body.appendChild(div);
 }


/**
 * 
 * @param {*} configs 
 */
 function createRectangleSeal(configs) {
    configs.style.width = configs.width ? configs.width : '100px';
    configs.style.height = configs.height ? configs.height : '40px';
    configs.style.writingMode = 'initial';
    createSquareSeal(configs, false);
 }

/**
 * 
 * @param {*} configs 
 */
 function createCircularSeal(configs) {
    const canvas = document.createElement('canvas');
    canvas.height = canvas.width = configs.size ? configs.size : '400';
    const context = canvas.getContext('2d');
    
    context.fillStyle="#ffffff";
    context.fillRect(0, 0, configs.size, configs.size);

    //    
    var width = canvas.width / 2;
    var height = canvas.height / 2;
    context.lineWidth = 4;
    context.strokeStyle = "#f00";
    context.beginPath();
    ///
    //context.arc(x,y,r,sAngle,eAngle,counterclockwise);
    //x,y:  rsAngleeAngle counterclockwiseFalse = true = 
    context.arc(width, height, configs.r, 0, Math.PI * 2);//
    // 
    context.stroke();

    create5star(context, width, height, 20, "#f00", 0);

    //   
    context.font = '8px ';
    context.textBaseline = 'middle';//
    context.textAlign = 'center'; //
    context.lineWidth=1;
    context.fillStyle = '#f00';
    context.save();
    context.translate(width, height+60);// ,
    context.scale(1, 1.8);//
    context.fillText(configs.text, 0, 0);//
    context.restore();

    //    
    context.translate(width, height);// ,
    context.font = '16px ';
    let count = configs.title.length;// 
    let angle = 4*Math.PI/(3*(count - 1));//    
    let chars = configs.title.split("");
    let c;
    for (let i = 0; i < count; i++) {
        c = chars[i];//  
//canvas  
        if (i == 0) {
            context.rotate(5 * Math.PI / 6);
        } else{
            context.rotate(angle);
        }
        context.save();
        context.translate(60, 0);// ,x
        context.rotate(Math.PI / 2);// 90,x
        context.scale(1, 1.8);//
        context.fillText(c, 0, 0);// 
        context.restore();
    }
    // 
    context.rotate(-Math.PI/6);
    context.translate(0-canvas.width/2, 0-canvas.height/2);
    document.body.appendChild(canvas);
 }

 //  
/** 
 * . (sx,sy),radius,rotate=0 
 * rotate:rotate 
 */
function create5star(context, sx, sy, radius, color, rotato) {
    context.save();
    context.fillStyle = color;
    context.translate(sx, sy);//
    context.rotate(Math.PI + rotato);//
    context.beginPath();//
    let x = Math.sin(0), y = Math.cos(0), dig = Math.PI / 5 * 4;
    for (let i = 0; i < 5; i++) {//
        x = Math.sin(i * dig);
        y = Math.cos(i * dig);
        context.lineTo(x * radius, y * radius);
    }
    context.closePath();
    context.stroke();
    context.fill();
    context.restore();
}

